@use "../../styles/partials/mixins" as *;

.guide-reasons {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1.5rem 1.5rem 10rem 1.5rem;

  &__title {
    padding: .5rem 0 .5rem 2.5rem;
    @include m-h1;

    @include tablet {
      @include d-h1;
      padding-top: 2rem;
      font-size: 7rem;
      line-height: 7rem;
      padding-bottom: 3rem;
    }
  }

  &__list-item {
    padding: 0.3rem 0.5rem;
    display: flex;
    align-items: center;

    @include m-p3;

    @include tablet {
      font-size: 1.5rem;
      line-height: 2rem;
    }
  }

  &__icon {
    margin-right: 1rem;

    @include tablet {
      margin-right: 2rem;
      width: 35px;
      height: 35px;
    }
  }
}