@use "../../styles/partials/mixins.scss" as *;

.sidebar {
	display: flex;
	flex-direction: column;
	align-items: flex-end;
	gap: 1rem;
	width: 100%;
	height: 100vh;
	padding: 0.75rem;
	text-align: start;
	background-color: var(--secondary-background);
	border-radius: 24px 0px 0px 0px;
	position: fixed;
	top: 0;
	right: 0;
	transition: all 0.6s ease;
	z-index: 2;
	@include tablet {
		width: 35%;
	}

	@include desktop {
		width: 20%;
	}

	&__title {
		font-size: 22px;
		font-weight: 700;
		padding: 8px 8px 8px 16px;
	}

	&__section {
		display: flex;
		flex-direction: column;
		width: 100%;
	}

	&__heading {
		font-size: 16px;
		font-weight: 500;
		padding: 18px 16px;
	}

	&__sub-section {
		display: flex;
		flex-direction: column;
		padding: 16px 24px;
		gap: 16px;
	}

	&__sub-heading {
		font-size: 14px;
		font-weight: 500;
	}

	&__divider {
		margin: 0px 16px;
		border-bottom: 1px solid rgb(45, 126, 45);
	}

	&--inactive {
		@extend .sidebar;
		right: -100%;
		// transition: 0.4s ease;

		// @include tablet {
		// 	transition: 0.3s;
		// }

		// @include desktop {
		// 	transition: 0.3s;
		// }
	}
}
